import React from 'react';
import './header.css';
import { Link } from 'react-router-dom';
import { ImSearch } from "react-icons/im";

import logo from './heading.png';
const header = () => {
  return (
    <nav className='header'>
        <img src={logo} className='logo'alt="BingeWatch" />
        <div className='topiccontainer'>
        
            <Link className='topic' to='/tvshows'>TV Shows</Link>
            <Link className='topic' to='/tvshows'>Movies</Link>
            <Link className='topic' to='/tvshows'>Recently Added</Link>
            
            

        </div>
        <ImSearch className='searchbutton'/>
    </nav>

  )
}

export default header