import React from 'react';
import { useEffect,useState } from 'react';
import './home.css';



const Banner = ({ arr = [] }) => {
    const [index, setIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setIndex(prevIndex => (prevIndex + 1) % arr.length);
        }, 10000);
        
        return () => clearInterval(interval);
    }, [arr.length]);

    if (arr.length === 0) {
        return null; // Or display a loading indicator
    }

    return (
        <div className='banner_row'>
            <div className='banner_info'>
                <h2 className='banner_info_title'>{arr[index].title}</h2>
                <div className='banner_info_overview'>{arr[index].overview}</div>
            </div>
            <div className='banner_image'>
                <img src={`https://image.tmdb.org/t/p/w500${arr[index].poster_path}`} alt="Movie Poster Not Available" />
            </div>
        </div>
    );
};


const Card = ({ img, title }) => {
    return (
        <div className='card'>
            <img src={`https://image.tmdb.org/t/p/w500${img}`} alt='Cover'></img>
            <div>{ title }</div>
        </div>
    );
}


const Row = ({title,arr = [ ]}) => {
    
    return(
        <div className='row'>
            <h2>{title}</h2>
            <div className='card_container'>
                {
                    arr.map((item,index)=>(
                        <Card key={index} img={item.poster_path} title={ item.title || item.name }/>
                    ))
                }
            </div>

        </div>
    )
}



